export default {
	name: 'TabControl',
	props: {
		titles: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data() {
		return {
			currentIndex: 0
		}
	},
	methods: {
		itemClick(index) {
			this.currentIndex = index;
			this.$emit('tab-click', index)
		}
	}
}
